@import "sass/index";

.betsBlock {
  background: use(--info-dark);

  border-radius: 20px;
  width: 100%;
  height: 428px;
  position: relative;

  & > div {
    width: 100%;

    & > div {
      width: 100%;
    }
  }

  @media (max-width: $mobileScreen) {
    height: 600px;

    & > div {
      & > div {
        overflow: hidden !important;
      }
    }
  }
}

.tableWrapper {
  padding: spacing(4);

  @media (max-width: $mobileScreen) {
    padding: spacing(1);
  }
}

.root {
  padding: 0;
}

.td {
  padding: 16px;
  border: none;

  &:last-child {
    width: 0;
  }

  @media (max-width: $mobileScreen) {
    padding: 10px 5px;
  }
}
