@import "sass/index";

.tableCell {
  border: none;
}
.tableCellWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.circle {
  display: flex;
  background: use(--secondary-main);

  border-radius: 50%;
}
.gameLogo {
  width: 48px;
  height: 48px;
}
.capitalize {
  text-transform: lowercase;
  display: block;

  &::first-letter {
    text-transform: uppercase;
  }
}
.gameType {
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 16px;
  color: use(--secondary-main);
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;
}
.userLogo {
  width: 28px;
  height: 28px;
}
.userName {
  padding-left: spacing(1.5);

  font-family: "Gilroy";
  font-weight: bold;

  font-size: 16px;
  color: use(--secondary-main);
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;
  cursor: pointer;
}
.description {
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 12px;
  color: use(--secondary-main);
  letter-spacing: 1px;
  opacity: 0.5;
  white-space: nowrap;
}
.betSize {
  padding-left: spacing(1.25);

  font-family: "Gilroy";
  font-weight: bold;

  font-size: 22px;
  color: use(--secondary-main);
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;
}
.lastCell {
  width: calc(280px + #{spacing(4)});
}
.chip {
  width: min-content;
  max-width: 100px;
  height: 30px;
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 18px;
  color: #150a30;
  background: #b5eb8c;
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;
  margin: 0 10px;
}
.grow {
  flex: 1 1 auto;
}
.coinWrapper {
  position: relative;
  width: 25px;
  height: 5px;
}
.coin {
  top: calc(50% - 8px);
  right: 0;
  width: 16px;
  height: 16px;
  color: black;
  cursor: default;
  position: absolute;
}
.profit {
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 22px;
  color: use(--subsidiary-green);
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;
}
.profitLose {
  color: use(--subsidiary-red);
}
