.img {
  position: absolute;
  right: 22px;
  top: 0;

  transform: scaleX(-1) translateY(-37px);
  pointer-events: none;

  @media (max-width: 480px) {
    width: 90%;
    transform: scaleX(-1) translateY(-30px);
  }

  @media (max-width: 350px) {
    width: 80%;
    transform: scaleX(-1) translateY(-25px);
  }
}
