@import "sass/index";

.root {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.tableCell {
  border: none;
  padding: 10px;
  width: max-content;
  max-width: 200px;
}
.tableCellWrapper {
  height: 100%;
  display: flex;
  width: fit-content;
  align-items: center;
  flex-direction: row;
}
.dataCellWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.circleWrapper {
  position: relative;
}
.nameWrapper {
  height: 100%;
  width: 100%;
}
.contentWrapper {
  max-width: 200px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &:first-child {
    margin-bottom: 1px;
  }
}
.coinWrapper {
  position: relative;
  width: 100%;
}
.coin {
  width: 14px;
  height: 14px;
  color: black;
  cursor: default;
  position: static;

  margin-right: 5px;
}
.circle {
  display: flex;
  background: use(--secondary-main);

  border-radius: 50%;
}

.gameType {
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 12px;
  color: use(--secondary-main);
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;

  padding-left: 5px;
  padding-bottom: 5px;

  width: fit-content;
}

.userName {
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 13px;
  color: use(--secondary-main);
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;
  padding-left: 5px;
  max-width: 100px;
  overflow: auto;
  width: fit-content;

  &::-webkit-scrollbar {
    display: none;
  }
}
.description {
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 10px;
  color: use(--secondary-main);
  letter-spacing: 1px;
  opacity: 0.5;
  white-space: nowrap;
}

.coinContainer {
  display: flex;
  align-items: center;
}
.betSize {
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 15px;
  color: use(--secondary-main);
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;

  display: flex;
  align-items: center;
}
.chip {
  margin-right: spacing(2.5);
  margin-left: 5px;
  width: 36px;
  height: 18px;
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 10px;
  color: #150a30;
  background: #b5eb8c;
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;

  @media (max-width: 390px) {
    margin-left: spacing(1.5);
    margin-right: 2.5px;
  }
}
.grow {
  flex: 1 1 auto;
}

.profit {
  font-family: "Gilroy";
  font-weight: bold;

  font-size: 15px;
  color: use(--subsidiary-green);
  letter-spacing: 0;
  line-height: 16px;
  white-space: nowrap;
}
